<template>
    <div class="main-page">
        <template v-if="showHeader">
            <q-card   class="page-section q-py-sm q-px-md q-mb-md nice-shadow-18">
                <div class="container">
                    <div class="row justify-between q-col-gutter-md">
                        <div class="col-12 col-md-auto ">
                            <div class="">
                                <div class="row  items-center q-col-gutter-sm q-px-sm">
                                    <div class="col">
                                        <div class="text-h6 text-primary">Edit Barangays</div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </q-card>
        </template>
        <section class="page-section q-mb-md">
            <div class="container">
                <div class="row q-col-gutter-md">
                    <div class="col-md-9 col-12 comp-grid">
                        <q-card   class="q-pa-md nice-shadow-18">
                            <div>
                                <div v-if="!loading">
                                    <ValidationObserver ref="observer" v-slot="{ invalid }" tag="form" @submit.prevent="startRecordUpdate()">
                                        <!--[form-content-start]-->
                                        <div class="row q-col-gutter-md">
                                            <div class="col-12">
                                                <div class="row">
                                                    <div class="col-sm-3 col-12">
                                                        Barangay *
                                                    </div>
                                                    <div class="col-sm-9 col-12">
                                                        <check-duplicate v-model="formData.barangay" check-path="components_data/barangays_barangay_exist/" v-slot="checker">
                                                        <ValidationProvider :rules="{required:true}" name="Barangay" v-slot="{ errors, invalid, validated }">
                                                            <q-select     outlined dense  ref="ctrlbarangay" emit-value map-options  @blur="checker.check" :loading="checker.loading" v-model="formData.barangay" :options="$menus.nameItems" label="Barangay"  :error="(invalid && validated) || checker.exist" :error-message="errors[0] || 'Not available'" >
                                                            </q-select> 
                                                        </ValidationProvider>
                                                        </check-duplicate>
                                                    </div>
                                                </div>
                                            </div>
                                            <div class="col-12">
                                                <div class="row">
                                                    <div class="col-sm-3 col-12">
                                                        City Id *
                                                    </div>
                                                    <div class="col-sm-9 col-12">
                                                        <api-data-source @loaded="response => mapOptionField(response, 'city_id')"  api-path="components_data/city_id_option_list"  v-slot="req">
                                                            <ValidationProvider :rules="{required:true}" name="City Id" v-slot="{ errors, invalid, validated }">
                                                                <q-select   :loading="req.loading"  outlined dense  ref="ctrlcity_id" emit-value map-options  v-model="formData.city_id" :options="req.response" label="City Id"  :error="invalid && validated" :error-message="errors[0]" >
                                                                </q-select> 
                                                            </ValidationProvider>
                                                        </api-data-source>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        <!--[form-content-end]-->
                                        <div v-if="showSubmitButton" class="text-center q-my-md">
                                            <q-btn    :rounded="false"  color="primary"  no-caps  unelevated   type="submit" :disabled="invalid" icon="send" :loading="saving">
                                                <q-spinner-oval slot="loading" /> Update
                                            </q-btn>
                                        </div>
                                    </ValidationObserver>
                                    <slot :submit="submit" :saving="saving"></slot>
                                </div>
                                <div v-else class="q-pa-sm text-center">
                                    <q-spinner
                                    :size="40"
                                    color="accent"
                                    indeterminate>
                                    </q-spinner>
                                    Loading...
                                </div>
                            </div>
                        </q-card>
                    </div>
                </div>
            </div>
        </section>
    </div>
</template>
<script>
	import { PageMixin } from "../../mixins/page.js";
	import { EditPageMixin } from "../../mixins/editpage.js";
	import { mapActions } from "vuex";
	export default {
		name: 'editBarangaysPage',
		components: {
		},
		mixins: [PageMixin, EditPageMixin ],
		props: {
			pageName: {
				type: String,
				default: 'barangays',
			},
			idName: {
				type: String,
				default: 'id',
			},
			routeName: {
				type: String,
				default: 'barangaysedit',
			},
			pagePath: {
				type : String,
				default : 'barangays/edit',
			},
			apiPath: {
				type: String,
				default: 'barangays/edit',
			},
		},
		data() {
            return {
				formData: {
					barangay: this.$utils.randomChars(10), city_id: "", 
				},
        	}
		},
		computed: {
			pageTitle:{
				get: function () {
					return "Edit Barangays"
				}
			}
		},
		meta () {
			return {
				title: this.pageTitle // set browser page title
			}
		},
		methods: {
			...mapActions('barangays', ['updateRecord', 'fetchRecord']),
			async startRecordUpdate(){
				const isFormValid = await this.$refs.observer.validate();
				if(isFormValid){
					this.saving = true;
					let id = this.id;
					let url = this.apiUrl;
					let payload = this.normalizedFormData();
					let data = { id, url, payload }
					this.updateRecord(data).then(
						(response) => {
							this.saving = false;
							this.flashMsg(this.msgAfterUpdate);
							this.resetForm();
							this.closeDialogs();// close page dialog that if opened
							this.navigateTo(`/barangays`);
						},
						(response) => {
							this.saving = false;
							this.showPageRequestError(response);
						}
					);
				}
			},
			updateFormFields: function(){
				//update form fields value after load from api
				//e.g convert fieldvalue (value,value2,value2) to array 
            },
			resetForm (){
				//reset form fields value
				this.formData = {barangay: this.$utils.randomChars(10), city_id: "", };
				requestAnimationFrame(() => {
					this.$refs.observer.reset();
				});
				//raise event to reset other custom form components
				this.$EventBus.$emit("resetForm");
			},
		},
	};
</script>
<style scoped>
</style>
